import { PushNotificationProvider } from './../../providers/push-notification/push-notification';
import { LoadingProvider } from './../../providers/loading/loading';
import { AuthProvider } from './../../providers/auth/auth';
import { GlobalProvider } from '../../providers/global/global';
import { Component, Input } from '@angular/core';
import { NavController, NavParams, AlertController, IonicPage } from 'ionic-angular';

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
  @Input() data: any;
  @Input() events: any;

  public email: string;
  public password: string;

  public errorEmailEmpty: boolean;
  public errorEmailInvalid: boolean;
  public errorPasswordEmpty: boolean;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams, 
    public global: GlobalProvider, 
    public auth: AuthProvider,
    public alertCtrl: AlertController,
    public loading: LoadingProvider,
    public pushNotification: PushNotificationProvider
  ) {
    
    this.errorEmailEmpty= false;
    this.errorEmailInvalid = false;
    this.errorPasswordEmpty = false;

    this.events =  {
      "onLogin" : this.onLogin
    }

  }

  onEvent = (event: string): void => {
    if (event == "onLogin" && !this.validate()) {
        return ;
    }
    if (this.events[event]) {
        this.events[event]({
            'email': this.email,
            'password': this.password
        });
    }        
  }

  validate():boolean {
    this.errorEmailEmpty = false;
    this.errorEmailInvalid = false;
    this.errorPasswordEmpty = false;
    let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (!this.email ||this.email.length == 0) {
        this.errorEmailEmpty = true;
    }
    else if (!re.test(String(this.email).toLowerCase())) {
      this.errorEmailInvalid = true;
    }

    if (!this.password || this.password.length == 0) {
        this.errorPasswordEmpty = true;
    }
    
    return !this.errorEmailEmpty && !this.errorEmailInvalid && !this.errorPasswordEmpty;
  }

  // login and go to home page
  onLogin = (params): void => {
    this.loading.show();

    this.auth.signin({email: this.email, password: this.password}).then(
      (data) => {        
        this.loading.hide();
        this.pushNotification.initialize();
        this.navCtrl.setRoot('HomePage');
      },
      (err) => {
        this.loading.hide();
        
        let message = "Không thể kết nối tới máy chủ!";
        if (err.error.message == 'invalid_email_or_password'){
          message = "Sai thông tin đăng nhập"
        }
        let alert = this.alertCtrl.create({
          title: 'Lỗi',
          message: message,
          buttons: [
            {
              text: 'OK',
              role: 'ok',
            }
          ]
        });
        alert.present();
      }
    );
  }

  openRegisterPage() {
    this.navCtrl.setRoot('RegisterPage');
  }

  openPasswordSendmailPage() {
    this.navCtrl.setRoot('PasswordSendmailPage');
  }

}



